$mediaSwitchTwoCol: 940px;


@media #{$media-desktop} {

	// hide owl-carousel dots when all 3 blog posts are/should be visible
	section.home-blog {
		.owl-controls {
			display: none;
		}
	}
}


@media #{$media-desktop}, #{$media-tablet} {

	section.home-services {
		section.service {

			&:nth-child(3n+3) {
				margin-right: 0;
			}
			&:nth-child(3n+1) {
				clear: left;
			}

			section {
				min-height: $home-service-text-min-height;
			}
		}
	}

