}

@media #{$media-tablet} {

	$marginColumn: $margin-def * 2;

	section.home-services {

		section.service {
			width: col-width-abs(3, $marginColumn);
			margin-right: $marginColumn;

			&:nth-child(3n+3) {
				margin-right: 0;
			}
			&:nth-child(3n+1) {
				clear: left;
			}
		}
	}

