}

@media #{$media-tablet} and (max-width: $mediaSwitchTwoCol) {

	$marginColumn: $margin-def * 2;

	section.home-services {

		section.service {
			width: col-width-abs(2, $marginColumn);
			margin-right: $marginColumn;

			&:nth-child(3n+3) {
				margin-right: $marginColumn;
			}
			&:nth-child(3n+1) {
				clear: none;
			}

			&:nth-child(2n+2) {
				margin-right: 0;
			}
			&:nth-child(2n+1) {
				clear: none;
			}

			section {
				min-height: $home-service-text-min-height + 1em;
			}
		}
	}

