// special for location/address: too small? change ratio
@media #{$media-tablet} and (max-width: 840px) {
	section.home-contact {
		margin-top: $margin-def * 2;
		width: col-width-abs(2, $margin-def * 2);
		margin-right: $margin-def * 2;
	}
	section.home-location {
		margin-top: $margin-def * 2;
		width: col-width-abs(2, $margin-def * 2);
	}
}


@media #{$media-phone} {

	section.home-services {

		section.service {
			float: none;
			clear: both;
			width: 100%;
			margin-right: 0;
			min-height: 0;
		}
	}

	section.home-blog {
		article.blog {
	// 		float: none;
	// 		clear: both;
	// 		width: 100%;
	// 		margin-right: 0;

	// 		// hide 2nd & 3rd post for now
	// 		&:nth-child(2),
	// 		&:nth-child(3) {
	// 			display: none;
	// 		}

			// from when landscape was only for phone
			// header .image {
			// 	padding-top: 50%;
			// }
		}
	}

	section.home-social {
		display: none;
	}

	section.home-contact,
	 section.home-location {
		float: none;
		clear: both;
		width: 100%;
	}

	section.home-contact {

		.h2 {
			font-size: pem(30);
		}

		.contact-form {
			max-width: none;
		}
	}

	section.home-location {
		margin-top: $margin-def * 4;
	}
}

// tweaks for some text not fitting in services titles
@media #{$media-desktop} and (max-width: 1150px) {
	section.home-services section.service .h2 {
		font-size: pem(20);
	}
}
@media (min-width: $mediaSwitchTwoCol) and (max-width: 960px) {
	section.home-services section.service .h2 {
		font-size: pem(20);
	}
}
@media (min-width: $width-phone) and (max-width: 670px) {
	section.home-services section.service .h2 {
		font-size: pem(22);
	}
}